/* Define styles for the news page */
.news-page {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    font-family: 'Arial', sans-serif;
  }
  
  header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  header h1 {
    font-size: 2em;
    margin-bottom: 10px;
  }
  
  .news-list {
    display: flex;
    flex-direction: column;
  }
  
  article {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 20px;
  }
  
  article h2 {
    margin-top: 0;
  }
  
  article span {
    color: #777;
  }
  