/* Define styles for the generator page */
.generator-page {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    font-family: 'Arial', sans-serif;
  }
  
  header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  header h1 {
    font-size: 2em;
    margin-bottom: 10px;
  }
  
  .generator-form p {
    margin-bottom: 10px;
  }
  
  .generator-form form {
    display: flex;
    flex-direction: column;
  }
  
  .generator-form input,
  .generator-form textarea {
    margin-bottom: 10px;
    padding: 5px;
  }
  
  .generator-form button {
    padding: 8px 20px;
    background-color: #333;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .generator-form button:hover {
    background-color: #555;
  }
  