/* Define styles for the homepage */
.homepage-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Arial', sans-serif;
}

header {
  text-align: center;
  margin-bottom: 20px;
}

header h1 {
  font-size: 2em;
  margin-bottom: 10px;
}

header p {
  color: #555;
}

section {
  margin-bottom: 30px;
}

h2 {
  color: #333;
}

footer {
  text-align: center;
  margin-top: 20px;
  color: #777;
}

.left-container {
  width: 60%;
  float: left;
  margin-right: 5%;
  padding: 20px;
  text-align: left;
  box-sizing: border-box;
  min-height: 600px;
  font-size: 6px;
  /*text color - hacker green*/
  color: #33ff33;
  padding-right: 10%;
  margin-left: -20%;
}

.right-container {
  width: 35%;
  float: right;
  padding: 20px;
  text-align: right;
  box-sizing: border-box;
  background-color: whitesmoke;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .left-container,
  .right-container {
      width: 100%;
      float: none;
      margin-right: 0;
  }

  .left-container,
  .right-container {
      margin: 5px;
      padding: 15px;
      min-height: auto; /* Reset minimum height for smaller screens */
  }
}
